.app-footer {
  background-color: var(--color-surface);
  padding: 15px;
  text-align: center;
  border-top: 1px solid #333;
  position: relative;
  width: 100%;
}

.footer-content {
  color: var(--color-text-secondary);
  font-size: 0.9rem;
}

.app-footer a {
  color: var(--color-primary);
  text-decoration: none;
  transition: color 0.2s;
}

.app-footer a:hover {
  text-decoration: underline;
}
