.export-dropdown-container {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.export-dropdown-backdrop {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: -1;
}

.export-dropdown {
  background-color: var(--color-surface);
  border-radius: 8px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.3);
  width: 300px;
  overflow: hidden;
  animation: dropdown-appear 0.2s ease-out;
}

@keyframes dropdown-appear {
  from {
    opacity: 0;
    transform: translateY(-20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.export-dropdown-header {
  padding: 15px;
  border-bottom: 1px solid var(--color-surface-light);
  text-align: center;
}

.export-dropdown-header h3 {
  margin: 0;
  font-size: 1.2rem;
  color: var(--color-text);
}

.export-dropdown-content {
  padding: 15px;
}

.export-repetitions {
  margin-bottom: 20px;
}

.export-repetitions label {
  display: block;
  margin-bottom: 8px;
  font-weight: 500;
  color: var(--color-text);
}

.repetition-control {
  display: flex;
  align-items: center;
  gap: 10px;
}

.repetition-button {
  width: 30px;
  height: 30px;
  border-radius: 50%;
  border: none;
  background-color: var(--color-surface-light);
  color: var(--color-text);
  font-size: 1.2rem;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: background-color 0.2s;
}

.repetition-button:hover:not(:disabled) {
  background-color: #444;
}

.repetition-button:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.repetition-control input {
  width: 50px;
  background-color: var(--color-surface-light);
  color: var(--color-text);
  border: 1px solid #444;
  border-radius: 4px;
  padding: 5px;
  text-align: center;
  font-size: 1rem;
}

.repetition-label {
  color: var(--color-text-secondary);
}

.export-format-label {
  margin-bottom: 10px;
  font-weight: 500;
  color: var(--color-text);
}

.export-dropdown-options {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.export-option {
  padding: 12px 15px;
  border: none;
  border-radius: 4px;
  font-size: 1rem;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s;
  display: flex;
  align-items: center;
  justify-content: center;
}

.export-option:hover {
  transform: translateY(-2px);
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}

.mp3-option {
  background-color: #8e44ad;
  color: white;
}

.mp3-option:hover {
  background-color: #9b59b6;
}

.midi-option {
  background-color: #2980b9;
  color: white;
}

.midi-option:hover {
  background-color: #3498db;
}
