.header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10px 20px;
  background-color: var(--color-surface);
  border-bottom: 1px solid var(--color-border);
  height: 60px;
}

.logo-container {
  display: flex;
  align-items: center;
}

.logo {
  height: 40px;
  margin-right: 10px;
}

.logo-container h2 {
  font-size: 1.5rem;
  font-weight: 700;
  color: var(--color-text);
  margin: 0;
}

.controls {
  display: flex;
  align-items: center;
  gap: 20px;
}

.play-button {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
  background-color: var(--color-primary);
  color: white;
  border: none;
  border-radius: 4px;
  padding: 8px 16px;
  font-weight: 600;
  transition: background-color 0.2s;
}

.play-button:hover {
  background-color: var(--color-primary-light);
}

.play-button.playing {
  background-color: var(--color-surface-light);
}

.bpm-control {
  display: flex;
  align-items: center;
  gap: 8px;
}

.bpm-control label {
  font-weight: 500;
}

.bpm-control input[type="number"] {
  width: 60px;
  background-color: var(--color-surface-light);
  color: var(--color-text);
  border: 1px solid var(--color-border);
  border-radius: 4px;
  padding: 4px 8px;
  text-align: center;
}

.bpm-slider {
  width: 120px;
  accent-color: var(--color-primary);
}

.action-buttons {
  display: flex;
  gap: 10px;
}

.clear-button,
.save-button,
.export-button {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
  background-color: var(--color-surface-light);
  color: var(--color-text);
  border: none;
  border-radius: 4px;
  padding: 8px 16px;
  font-weight: 500;
  transition: background-color 0.2s;
}

.demo-button {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
  background-color: #2c3e50;
  color: white;
  border: none;
  border-radius: 4px;
  padding: 8px 16px;
  font-weight: 500;
  transition: all 0.2s;
  position: relative;
  overflow: hidden;
}

.demo-button::after {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.2), transparent);
  transition: 0.5s;
}

.demo-button:hover::after {
  left: 100%;
}

.clear-button:hover,
.save-button:hover,
.export-button:hover {
  background-color: #444;
}

.export-button {
  background-color: #8e44ad;
  color: white;
  position: relative;
  overflow: hidden;
  transition: all 0.2s;
}

.export-button:hover {
  background-color: #9b59b6;
  transform: translateY(-2px);
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}

.export-button::after {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.2), transparent);
  transition: 0.5s;
}

.export-button:hover::after {
  left: 100%;
}

.demo-button:hover {
  background-color: #34495e;
  transform: translateY(-2px);
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}

@media (max-width: 768px) {
  .header {
    flex-direction: column;
    height: auto;
    padding: 10px;
    gap: 10px;
  }
  
  .controls,
  .action-buttons {
    width: 100%;
    justify-content: center;
  }
  
  .bpm-slider {
    width: 80px;
  }
}
