.sequencer-grid {
  display: flex;
  flex-direction: column;
  background-color: var(--color-surface);
  border-radius: 4px;
  margin: 10px;
  overflow: auto;
}

.tracks-container {
  display: flex;
  flex-direction: column;
  gap: 2px;
  padding: 10px;
  overflow-y: visible;
}

.track {
  display: flex;
  align-items: center;
  background-color: var(--color-surface-light);
  border-radius: 4px;
  padding: 8px;
  cursor: pointer;
  transition: background-color 0.2s;
  height: 50px; /* Increased height to give more vertical space */
  margin-bottom: 2px;
}

.track:hover {
  background-color: #444;
}

.track.selected {
  background-color: #444;
  /* Remove the border-left that might be causing the shift */
  /* Use a different visual indicator that doesn't affect layout */
  box-shadow: inset 3px 0 0 var(--color-primary);
  /* Ensure consistent padding and sizing */
  padding: 8px; /* Match the padding of the regular track */
  box-sizing: border-box;
}

.track-controls {
  display: flex;
  align-items: center;
  gap: 5px;
  width: 190px; /* Increased width to accommodate the drag handle */
  padding-right: 10px;
  flex-shrink: 0;
}

.drag-handle {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 20px;
  height: 20px;
  color: var(--color-text-secondary);
  cursor: grab;
  opacity: 0.6;
  transition: opacity 0.2s, color 0.2s;
}

.drag-handle:hover {
  opacity: 1;
  color: var(--color-text);
}

.track.dragging {
  background-color: var(--color-surface);
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.3);
  opacity: 0.8;
  transform: scale(1.02);
  z-index: 10;
}

.track-number {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 32px;
  height: 32px;
  background-color: #222;
  border-radius: 4px;
  font-weight: 600;
  overflow: hidden;
  margin-right: 5px;
}

.track-icon {
  width: 24px;
  height: 24px;
  object-fit: contain;
  border-radius: 4px;
  image-rendering: -webkit-optimize-contrast;
  image-rendering: crisp-edges;
  transform: translateZ(0);
  backface-visibility: hidden;
}

.track-controls-right {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly; /* Evenly distribute space */
  height: 100%; /* Take full height of the track */
  padding: 3px 0; /* Added vertical padding */
}

.track-buttons {
  display: flex;
  gap: 6px; /* Slightly increased gap between buttons */
  justify-content: center;
}

.track-button {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 24px;
  height: 24px;
  background-color: transparent;
  border: none;
  color: var(--color-text);
  cursor: pointer;
  transition: all 0.2s;
  border-radius: 4px;
}

.track-button:hover {
  background-color: rgba(255, 255, 255, 0.1);
}

.mute-button.muted {
  color: var(--color-error);
  background-color: rgba(255, 0, 0, 0.1);
}

.solo-button.active {
  color: #FFD700; /* Gold */
  background-color: rgba(255, 215, 0, 0.1);
}

.lock-button.active {
  color: #4169E1; /* Royal Blue */
  background-color: rgba(65, 105, 225, 0.1);
}

.volume-slider {
  width: 60px;
  accent-color: var(--color-primary);
  margin: 0 auto; /* Center horizontally */
}

.steps {
  display: flex;
  flex: 1;
  gap: 2px;
  overflow-x: auto;
}

.step {
  width: 30px;
  height: 36px; /* Increased to match the track height better */
  background-color: #222;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.2s;
  flex-shrink: 0;
}

.step:hover {
  background-color: #333;
}

.step.active {
  background-color: var(--color-primary);
}

.step.current {
  border: 2px solid white;
  box-shadow: 0 0 5px rgba(255, 255, 255, 0.5);
}

.step.active.current {
  background-color: var(--color-primary-light);
}

.track-actions-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 10px;
}

.track-actions {
  display: flex;
  justify-content: center;
  gap: 10px;
  margin-bottom: 10px;
}

.track-instructions {
  font-size: 0.9rem;
  color: #e0e0e0;
  text-align: center;
  width: 100%;
  line-height: 1.4;
  background-color: #1e2a1e;
  padding: 10px;
  border-radius: 4px;
  border-left: 4px solid #2a3a2a;
  margin-top: 5px;
}

.add-track-button,
.remove-track-button {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 40px;
  height: 40px;
  background-color: var(--color-surface-light);
  border: none;
  border-radius: 50%;
  color: var(--color-text);
  font-size: 1.2rem;
  cursor: pointer;
  transition: all 0.2s;
}

.add-track-button:hover {
  background-color: var(--color-primary);
  color: white;
}

.remove-track-button:hover {
  background-color: var(--color-error);
  color: white;
}

.step-numbers {
  display: flex;
  padding: 10px 0 5px 0;
  margin-left: 190px; /* Match the track-controls width + adjustment for perfect centering */
  gap: 2px;
  overflow-x: auto;
  margin-bottom: 5px;
}

.step-number {
  width: 30px;
  height: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 0.8rem;
  color: var(--color-text-secondary);
  flex-shrink: 0;
}

/* Highlight every 4th step number */
.step-number:nth-child(4n) {
  color: var(--color-primary);
  font-weight: bold;
}

/* Highlight every 4th step for better readability */
.step-number:nth-child(4n+1),
.step:nth-child(4n+1) {
  border-left: 1px solid #444;
}


@media (max-width: 768px) {
  .track-controls {
    width: 160px;
  }
  
  .step {
    width: 25px;
    height: 25px;
  }
  
  .step-number {
    width: 25px;
  }
  
  .step-numbers {
    margin-left: 190px; /* Match the track-controls width + adjustment for perfect centering */
    padding-left: 0;
    padding-right: 0;
  }
}
