.app {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  width: 100%;
}

.main-content {
  display: flex;
  flex-direction: column;
  flex: 1;
  overflow: auto;
}

.sequencer-section {
  flex: 0 0 auto;
  min-height: 450px; /* Ensure enough space for all 8 tracks */
  overflow-y: auto;
  padding-bottom: 20px;
}

.browser-section {
  flex: 1 1 auto;
  overflow-y: auto;
  min-height: 600px; /* Match the height of the sample browser */
}

@media (min-width: 768px) {
  .main-content {
    height: calc(100% - 60px); /* Adjust based on header height */
  }
}
